import uniDrawer from '@/components/uni-ui/uni-drawer/uni-drawer.vue'
import card from '@/components/common/card.vue'
import radioGroups from '@/components/common/radio-group.vue'
import searchList from '@/components/search-list/search-list.vue'
export default {
	components: {
		card,
		uniDrawer,
		radioGroups,
		searchList
	},
	data() {
		return {
			loadingText: '上拉加载更多',
			showRight: false,
			keyword: '',
			page: 1,
			condition: {},
			screen: {
				currentIndex: 0,
				list: [{
						name: '综合',
						key: 'all',
						status: 1
					},
					{
						name: '销量',
						key: 'sale_count',
						status: 0
					},
					{
						name: '价格',
						key: 'min_price',
						status: 0
					}
				]
			},
			label: {
				selected: 0,
				list: [{
						name: '不限',
						rule: false,
						value: false
					}, {
						name: '0-50',
						rule: 'between',
						value: '0,50'
					},
					{
						name: '50-100',
						rule: 'between',
						value: '50,100'
					},
					{
						name: '100-500',
						rule: 'between',
						value: '100,500'
					},
					{
						name: '500-1000',
						rule: 'between',
						value: '500,1000'
					},
					{
						name: '大于1000',
						rule: '>',
						value: '1000'
					}
				]
			},
			oldSelected: 0,
			list: [{
				title: '真无线蓝牙耳机',
				titlepic: '/static/images/demo/demo6.jpg',
				desc: '雅致简约 / 分体式入耳 / 收纳盒充电 / 蓝牙5.0 / 触控操作',
				pprice: '999',
				comment_num: '990',
				good_num: '99%'
			}],
			historyList: []
		}
	},
	computed: {
		// 排序相关
		options() {
			let obj = this.screen.list[this.screen.currentIndex]
			let value = obj.status === 1 ? 'asc' : 'desc'
			return {
				[obj.key]: value
			}
		}
	},
	onNavigationBarButtonTap(e) {
		if (e.index === 0) {
			this.search()
		}
	},
	onNavigationBarSearchInputChanged(e) {
		this.keyword = e.text
	},
	onNavigationBarSearchInputConfirmed() {
		this.search()




		uni.hideKeyboard()

	},
	onLoad(e) {
		this.keyword = e.keyword
		// 修改搜索占位符








		// 加载数据
		this.getData()
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.getData(true, () => {
			uni.stopPullDownRefresh()
			uni.showToast({
				title: '刷新成功',
				icon: 'none'
			});
		})
	},
	// 上拉加载
	onReachBottom() {
		if (this.loadingText !== '上拉加载更多') return
		this.page++
		this.loadingText = '加载中...'
		this.getData()
	},

	methods: {
		// 初始化数据
		__init() {
			this.page = 1
			this.list = []
			this.label.selected = 0
			this.oldSelected = 0
			this.condition = {}
		},
		showDrawer() {
			this.showRight = true
			// 记录已有的值
			this.oldSelected = this.label.selected
		},
		hideDrawere() {
			this.label.selected = this.oldSelected
			// 恢复原有的值
			this.showRight = true
			// 旧的值重置为0
			this.oldSelected = 0
		},
		// 组织筛选条件
		getCondition() {
			let item = this.label.list[this.label.selected]
			this.condition = (item.rule && item.value) ? {
				price: item.rule + ',' + item.value
			} : {}
		},
		// 重置筛选条件
		reset() {
			this.condition = {}
			this.label.selected = 0
			// 获取数据
			this.getData()
			this.showRight = false
		},
		// 提交筛选条件
		comfrim() {
			this.getCondition()
			// 获取数据
			this.getData()
			this.showRight = false
		},
		// 加载数据
		async getData(refresh = false, callback) {
			let page = refresh ? 1 : this.page
			let params = {
				title: this.keyword,
				page,
				...this.options,
				...this.condition
			}
			let res = await this.$http.post('/goods/search', params)
			let list = this.format(res)
			this.list = this.page === 1 ? [...list] : [...this.list, ...list]
			this.loadingText = list.length < 10 ? '没有更多了' : '上拉加载更多'
			if (typeof callback === 'function') {
				callback()
			}
		},
		// 格式化
		format(res) {
			return res.map(v => {
				let good_num = v.comments_count === 0 ? 0 : (v.comments_good_count / v.comments_count) * 100
				return {
					id: v.id,
					title: v.title,
					titlepic: v.cover,
					desc: v.desc,
					pprice: v.min_price,
					comment_num: v.comments_count,
					good_num: good_num.toFixed(2) + '%'
				}
			})
		},
		changeScreem(index) {
			// 判断当前点击是否已经是激活状态
			let oldIndex = this.screen.currentIndex
			let oldItem = this.screen.list[oldIndex]
			if (oldIndex === index) {
				oldItem.status = oldItem.status === 1 ? 2 : 1
			} else {
				let newItem = this.screen.list[index]
				// 移除旧激活状态
				oldItem.status = 0
				this.screen.currentIndex = index
				// 增加新激活状态
				newItem.status = 1
			}

			this.__init()
			// 加载数据
			this.getData()
		},
		changeRadio(index) {
			console.log('parant changeRadio')
			this.label.selected = index
		},
		searchComfirm() {
			
			this.search()
		},
		search() {
			this.__init()
			if (this.keyword === '') {
				return uni.showToast({
					title: '关键词不能为空',
					icon: 'none'
				});
			}
			this.addHistory()
			this.__init()
			this.getData()
			uni.$emit('refresh', this.keyword)
		},
		// 加入搜索记录
		addHistory() {
			let historyList = uni.getStorageSync("searchHistory") ? JSON.parse(uni.getStorageSync("searchHistory")) : []
			let index = historyList.indexOf(this.keyword)
			if (index === -1) {
				historyList.unshift(this.keyword)
			} else {
				historyList.unshift(historyList.splice(index, 1)[0])
			}
			uni.setStorageSync('searchHistory', JSON.stringify(historyList))
		},
	}
}
